import config from '../config';
import { common } from '@material-ui/core/colors';
import request from 'superagent';

const url = config.helperApi;
class HelperApiRepository {
  static async getRedirectUrl({
    title, 
    comment, 
    email, 
    name, 
    price, 
  }: {
    title: string, 
    comment: string, 
    email: string, 
    name: string, 
    price: number, 
  }) : Promise<string> {
    return url + `/donations?email=${encodeURIComponent(email)}&name=${encodeURIComponent(name)}&price=${price}&title=${encodeURIComponent(title)}&comment=${encodeURIComponent(comment)}`;
  }
  // static async getArticle(slug: string, ) : Promise<ArticleResponse | null> {
  //   try {
  //     const res = superagent
  //       .get(config.api + `${url}/${slug}`)
  //       .set("accept", "json")
  //       .set({'Content-Type': 'application/json'})
  //       if(token)
  //         res.set({'Authorization': 'Bearer ' + token})   
  //     const response = await res.send();

  //     const article = response.body as ArticleResponse;
  //     return article;
  //   } catch(err) {
  //     console.log(err);
  //     return null;
  //   }
  // }
  static async postPaypalCheckout({
    title, 
    comment, 
    email, 
    name, 
    price,
    orderId,
  }: {
    title: string, 
    comment: string, 
    email: string, 
    name: string, 
    price: number, 
    orderId: string,
  }) : Promise<any> {
    const res = await superagent.post(url + "/donations/paypal")
      .send({
        title: title,
        email: email,
        name: name,
        comment: comment,
        price: price,
        orderId: orderId,
      });
    return res.body;
  }
}

export default HelperApiRepository;